/* global console, document, Excel, Office, bootstrap */
// You can specify which plugins you need
const bootstrap = require("bootstrap");
import {
  excelDateToJSDate,
  jsDateToExcelDate,
  showNotification,
  STATUS,
  checkTaskIdExists,
  convertDecimalToTime,
  logWork,
} from "../commons/common.js";

Office.onReady((info) => {
  if (info.host === Office.HostType.Excel) {
    // initData();

    loadAccounts();
    getTasksByAccount();
    document.getElementById("accountSelect").onchange = loadFullname;

    //Test
    document.getElementById("logWorkModal").addEventListener("shown.bs.modal", (event) => {
      const button = event.relatedTarget;

      const now = new Date();
      const year = now.getFullYear();
      const month = String(now.getMonth() + 1).padStart(2, "0"); // Tháng bắt đầu từ 0 nên cần +1
      const day = String(now.getDate()).padStart(2, "0");
      const currentDate = `${year}-${month}-${day}`;

      const startTime = "08:00";
      const endTime = "17:00";

      document.getElementById("inputDate").value = currentDate;
      let data = {
        date: currentDate,
        taskID: null,
        pic: null,
        startTime: startTime,
        endTime: endTime,
      };
      if (button) {
        const taskID = button.getAttribute("data-taskid");
        const taskPic = button.getAttribute("data-taskpic");
        document.getElementById("inputTaskID").value = taskID;
        document.getElementById("inputPIC").value = taskPic;
        data.taskID = taskID;
        data.pic = taskPic;
      }
      document.getElementById("inputStartTime").value = startTime;
      document.getElementById("inputEndTime").value = endTime;
      checkTaskIdExists(data).then((result) => {
        if (result.rowIndex !== -1) {
          document.getElementById("logwork-info").innerText = "UPDATE: check StartTime and EndTime before submitting";
          document.getElementById("inputStartTime").value = convertDecimalToTime(result.startTime);
          document.getElementById("inputEndTime").value = convertDecimalToTime(result.endTime);
          document.getElementById("logwork-rowIndex").value = result.rowIndex;
        } else {
          document.getElementById("logwork-rowIndex").value = -1;
          document.getElementById("logwork-info").innerText = "NEW: check information before submitting";
        }
      });

      document.getElementById("btn-startTime-now").addEventListener("click", function () {
        const now = new Date();
        const hours = String(now.getHours()).padStart(2, "0");
        const minutes = String(now.getMinutes()).padStart(2, "0");
        const currentTime = `${hours}:${minutes}`;
        document.getElementById("inputStartTime").value = currentTime;
      });
      document.getElementById("btn-endTime-now").addEventListener("click", function () {
        const now = new Date();
        const hours = String(now.getHours()).padStart(2, "0");
        const minutes = String(now.getMinutes()).padStart(2, "0");
        const currentTime = `${hours}:${minutes}`;
        document.getElementById("inputEndTime").value = currentTime;
      });
    });

    document.getElementById("btnStartTime").addEventListener("click", () => {
      const data = {
        rowIndex: document.getElementById("logwork-rowIndex").value,
        date: document.getElementById("inputDate").value,
        taskID: document.getElementById("inputTaskID").value,
        pic: document.getElementById("inputPIC").value,
        startTime: document.getElementById("inputStartTime").value,
        endTime: document.getElementById("inputEndTime").value,
        desc: document.getElementById("inputDesc").value,
      };

      showNotification("data:" + JSON.stringify(data));
      logWork(data);
      const myModalEl = document.getElementById("logWorkModal");
      const modal = bootstrap.Modal.getInstance(myModalEl);
      modal.hide(); // Close the modal after submission
    });

    // Add click event listener to the "Update Progress" button
    document.getElementById("btnUpdateProgress").addEventListener("click", function () {
      // Get the task ID from the input field
      const taskId = document.getElementById("progressTaskID").value;

      // Create an object with updated task data from form inputs
      const taskData = {
        // get plan value
        pic: document.getElementById("pic").value,
        reviewer: document.getElementById("reviewer").value,
        pStartDate: document.getElementById("pStartDate").value,
        pEndDate: document.getElementById("pEndDate").value,
        pEffort: document.getElementById("pEffort").value,

        // get actual value
        startDate: document.getElementById("aStartDate").value,
        endDate: document.getElementById("aEndDate").value,
        effort: document.getElementById("aEffort").value,
        percentCompleted: document.getElementById("percentACompleted").value,
        releaseDate: document.getElementById("aReleaseDate").value,

        // get task note
        taskNote: document.getElementById("taskNote").value,

        // get link value
        linkJR: document.getElementById("linkJira").value,
        linkBD: document.getElementById("linkBD").value,
        linkDD: document.getElementById("linkDD").value,
        linkET: document.getElementById("linkEstimatedTask").value,
        linkED: document.getElementById("linkEvidence").value,
        linkFR: document.getElementById("linkFileReview").value,
      };

      // Fetch the task by ID (possibly to validate or prepare for update)
      getTaskByID(taskId);

      // Update the task details with the new data
      updateTaskDetails(taskId, taskData);
      loadFullname();

      // Close the modal after update
      let myModalElement = document.getElementById("updateProgressModal");

      // Try to get an instance of the Bootstrap modal
      // eslint-disable-next-line no-undef
      let myModal = bootstrap.Modal.getInstance(myModalElement);

      if (myModal) {
        // If an instance exists, hide the modal
        myModal.hide();
      } else {
        // If no instance exists, create a new one and hide it
        // eslint-disable-next-line no-undef
        let newModal = new bootstrap.Modal(myModalElement);
        newModal.hide();
      }
    });

    let myModalElement = document.getElementById("updateProgressModal");
    myModalElement.addEventListener("hidden.bs.modal", function () {
      // Các hành động cần thiết sau khi modal đã đóng
      console.log("Modal đã đóng.");
    });

    // Wait for the DOM to be fully loaded before executing the script
    document.addEventListener("DOMContentLoaded", function () {
      // Set the default date for the logwork form to today's date
      setDefaultLogworkDate();
    });
  }
});

/**
 * Sets the default date for the logwork form to today's date.
 */
function setDefaultLogworkDate() {
  // Get today's date in YYYY-MM-DD format
  const today = getTodayDate();

  // Set the value of the date input field
  const dateInput = document.getElementById("logworkForm_date");
  if (dateInput) {
    dateInput.value = today;
  } else {
    console.warn("Logwork date input field not found");
  }
}

/**
 * Returns today's date in YYYY-MM-DD format.
 * @returns {string} Today's date in YYYY-MM-DD format
 */
function getTodayDate() {
  return new Date().toISOString().split("T")[0];
}

function updateTaskDetails(taskID, taskData) {
  Excel.run(function (context) {
    const sheet = context.workbook.worksheets.getItem("WBS");
    const table = sheet.tables.getItem("TblWbs");
    const range = table.getDataBodyRange();
    range.load("values");

    return context.sync().then(function () {
      const data = range.values;
      const taskIndex = data.findIndex((row) => row[0] === taskID); // Tìm index của task

      if (taskIndex !== -1) {
        // Tạo một mảng mới với các giá trị cập nhật
        const updatedRow = [...data[taskIndex]];

        // Update value for plan section
        updatedRow[3] = taskData.pic;
        updatedRow[4] = taskData.reviewer;
        updatedRow[5] = jsDateToExcelDate(taskData.pStartDate);
        updatedRow[6] = jsDateToExcelDate(taskData.pEndDate);
        updatedRow[8] = taskData.pEffort;

        // Update value for actual section
        updatedRow[9] = jsDateToExcelDate(taskData.startDate);
        updatedRow[10] = jsDateToExcelDate(taskData.endDate);
        updatedRow[11] = taskData.effort;
        updatedRow[12] = taskData.percentCompleted / 100;
        updatedRow[13] = jsDateToExcelDate(taskData.releaseDate);

        updatedRow[23] = taskData.taskNote;

        // Cập nhật toàn bộ hàng
        range.getRow(taskIndex).values = [updatedRow];

        // Update value for link
        const cellIndices = [16, 17, 18, 19, 20, 21];
        const hyperlinks = [
          { textToDisplay: "Link", screenTip: "Link Jira", address: taskData.linkJR },
          { textToDisplay: "Link", screenTip: "Link BD", address: taskData.linkBD },
          { textToDisplay: "Link", screenTip: "Link DD", address: taskData.linkDD },
          { textToDisplay: "Link", screenTip: "Link Estimation Task", address: taskData.linkET },
          { textToDisplay: "Link", screenTip: "Link Evidence", address: taskData.linkED },
          { textToDisplay: "Link", screenTip: "Link File Review", address: taskData.linkFR },
        ];

        cellIndices.forEach((index, i) => {
          let cellRange = range.getCell(taskIndex, index);
          let hyperlink = hyperlinks[i];
          if (hyperlink.address) {
            // Kiểm tra nếu taskData có giá trị
            cellRange.set({
              hyperlink: hyperlink,
            });
          } else {
            cellRange.values = [[""]];
          }
        });

        return context.sync().then(function () {
          showNotification("Update Successfully", "success");
        });
      } else {
        showNotification("Không tìm thấy thông tin: " + taskID, "danger");
      }
    });
  }).catch(function (error) {
    console.log(error);
    showNotification("Error updating task details: " + error.message, "danger");
  });
}

function getTaskByID(taskId) {
  Excel.run(function (context) {
    const sheet = context.workbook.worksheets.getItem("WBS");
    const table = sheet.tables.getItem("TblWbs");
    const range = table.getDataBodyRange();
    range.load(["values"]);

    const sheetSetting = context.workbook.worksheets.getItem("Setting");
    const domain = sheetSetting.getRange("B3");
    domain.load("values");

    return context.sync().then(function () {
      const values = range.values;

      const task = values.find((row) => row[0] === taskId); // Tìm task dựa trên TaskID
      const taskIndex = values.findIndex((row) => row[0] === taskId); // Lấy index của task
      if (task) {
        document.querySelector("#progressForm #progressTaskID").value = taskId;

        const linkToBack = document.querySelector("#linkToBack");
        const backlogUrl = domain.values[0][0];
        linkToBack.href = `${backlogUrl}${taskId}`;
        linkToBack.textContent = `Open Backlog`;
        linkToBack.target = "_blank";
        linkToBack.rel = "noopener noreferrer";

        // For Plan Section
        document.querySelector("#progressForm #pic").value = task[3]; // PIC
        document.querySelector("#progressForm #reviewer").value = task[4]; // Reviewer
        document.querySelector("#progressForm #pStartDate").value = excelDateToJSDate(task[5]); // P.StartDate
        document.querySelector("#progressForm #pEndDate").value = excelDateToJSDate(task[6]); // P.EndDate
        document.querySelector("#progressForm #pEffort").value = task[8]; // P.Effort

        // For Actual Section
        document.querySelector("#progressForm #aStartDate").value = excelDateToJSDate(task[9]); // A.StartDate
        document.querySelector("#progressForm #aEndDate").value = excelDateToJSDate(task[10]); // A.EndDate
        document.querySelector("#progressForm #aEffort").value = task[11]; // A.Effort
        document.querySelector("#progressForm #percentACompleted").value = task[12] * 100; // %A.Completed
        document.querySelector("#progressForm #aReleaseDate").value = excelDateToJSDate(task[13]); // A.ReleaseDate

        // taskNote
        document.querySelector("#progressForm #taskNote").value = task[23]; // Note

        // Lấy hyperlink từ giá trị hoặc từ đối tượng hyperlink nếu có
        // Link Jira
        const cellIndices = [16, 17, 18, 19, 20, 21];
        const formFields = ["linkJira", "linkBD", "linkDD", "linkEstimatedTask", "linkEvidence", "linkFileReview"];

        const cellRanges = cellIndices.map((index) => {
          let cellRange = range.getCell(taskIndex, index);
          cellRange.load("hyperlink");
          return cellRange;
        });

        return context.sync().then(function () {
          cellRanges.forEach((cellRange, i) => {
            let hyperlink = cellRange.hyperlink;
            let value = hyperlink ? hyperlink.address : task[cellIndices[i]];
            document.querySelector(`#progressForm #${formFields[i]}`).value = value;
          });
        });
      } else {
        showNotification("không tìm thấy thông tin: " + taskId, "danger");
      }
    });
  }).catch(function (error) {
    console.log(error);
  });
}

function initData() {
  Excel.run(function (context) {
    var sheet = context.workbook.worksheets.getItem("Setting");
    var range = sheet.getRange("B1:B5");

    range.load("values");
    return context.sync().then(function () {
      var project_code = range.values[0];
      var project_manager = range.values[1];
      document.getElementById("v_project_code").innerText = project_code;
      document.getElementById("v_project_manager").innerText = project_manager;
    });
  }).catch(function (error) {
    console.log("Error: " + error);
  });
}

function loadAccounts() {
  Excel.run(function (context) {
    let sheet = context.workbook.worksheets.getItem("Resource");
    let table = sheet.tables.getItem("TblResource");
    let range = table.getRange();

    range.load("values");

    return context.sync().then(function () {
      let data = range.values; // Lấy toàn bộ giá trị trong bảng
      let accountSelect = document.getElementById("accountSelect");
      let headers = data[0];
      let accountIndex = headers.indexOf("Account");
      // Lặp qua dữ liệu để thêm vào combobox
      for (let i = 1; i < data.length; i++) {
        // Bỏ qua hàng tiêu đề
        let account = data[i][accountIndex]; // Column Account (index 1)
        let option = document.createElement("option");
        option.value = account;
        option.text = account;
        accountSelect.appendChild(option);
      }
    });
  }).catch(function (error) {
    console.log("Error: " + error);
  });
}

function loadFullname() {
  let accountSelect = document.getElementById("accountSelect");
  let selectedAccount = accountSelect.value;
  let fullnameDisplay = document.getElementById("fullnameDisplay");

  if (selectedAccount) {
    Excel.run(function (context) {
      let sheet = context.workbook.worksheets.getItem("Resource");
      let table = sheet.tables.getItem("TblResource");
      let range = table.getRange();

      range.load("values");

      return context.sync().then(function () {
        let data = range.values;
        let headers = data[0];

        // Tìm fullname tương ứng với account đã chọn
        for (let i = 1; i < data.length; i++) {
          // Bỏ qua hàng tiêu đề
          if (data[i][headers.indexOf("Account")] === selectedAccount) {
            // Column Account (index 1)
            fullnameDisplay.innerText = data[i][headers.indexOf("Fullname")]; // Column Fullname (index 2)
            getTasksByAccount();
            return;
          }
        }
        fullnameDisplay.innerText = ""; // Nếu không tìm thấy, xóa nội dung
      });
    }).catch(function (error) {
      console.log("Error: " + error);
    });
  } else {
    fullnameDisplay.innerText = ""; // Nếu không chọn gì, xóa nội dung
    getTasksByAccount();
  }
}

function getTasksByAccount() {
  const selectedAccount = document.getElementById("accountSelect").value.trim().toLowerCase();

  Excel.run(function (context) {
    const sheet = context.workbook.worksheets.getItem("WBS");
    const table = sheet.tables.getItem("TblWbs");
    const range = table.getRange();
    range.load("values");

    return context.sync().then(function () {
      const data = range.values;

      // Update lại toàn bộ giá trị tại cell 16, 17, 18, 19 là giá trị hyperlink
      const cellIndices = [16, 17, 18, 19, 20, 21];
      const cellRanges = data
        .map((row, rowIndex) => {
          return cellIndices.map((index) => {
            let cellRange = range.getCell(rowIndex, index);
            cellRange.load(["hyperlink", "rowIndex", "columnIndex"]);
            return cellRange;
          });
        })
        .flat();

      return context.sync().then(function () {
        cellRanges.forEach((cellRange) => {
          let hyperlink = cellRange.hyperlink;
          if (hyperlink) {
            let rowIndex = cellRange.rowIndex;
            let colIndex = cellRange.columnIndex;
            data[rowIndex][colIndex] = hyperlink.address;
          }
        });

        const groupedTasks = processTaskData(data, selectedAccount);
        updateUIWithTaskData(groupedTasks);
      });
    });
  }).catch(function (error) {
    console.error("Error retrieving tasks:", error);
    showNotification("Error retrieving tasks:" + error, "danger");
  });
}

function processTaskData(data, selectedAccount) {
  const groupedTasks = {
    "Tổng nhiệm vụ": [],
    "Task doing": [],
    "Task notDone": [],
    "Task review": [],
    "Task release": [],
    "Task released": [],
  };

  data.forEach((row) => {
    const pic = row[3].trim().toLowerCase();
    const reviewer = row[4].trim().toLowerCase();
    const completed = row[12];

    if (shouldIncludeTask(pic, reviewer, selectedAccount)) {
      categorizeTask(row, completed, groupedTasks);
    }
  });

  return groupedTasks;
}

function shouldIncludeTask(pic, reviewer, selectedAccount) {
  return selectedAccount === "" || pic === selectedAccount || reviewer === selectedAccount;
}

function categorizeTask(row, completed, groupedTasks) {
  groupedTasks["Tổng nhiệm vụ"].push(row);

  if (completed > 0 && completed < 0.8) {
    groupedTasks["Task doing"].push(row);
  } else if (completed === 0) {
    groupedTasks["Task notDone"].push(row);
  } else if (completed === 0.8) {
    groupedTasks["Task review"].push(row);
  } else if (completed === 0.9) {
    groupedTasks["Task release"].push(row);
  } else if (completed === 1) {
    groupedTasks["Task released"].push(row);
  }
}

function updateUIWithTaskData(groupedTasks) {
  const counts = {
    total: groupedTasks["Tổng nhiệm vụ"].length,
    doing: groupedTasks["Task doing"].length,
    notDone: groupedTasks["Task notDone"].length,
    review: groupedTasks["Task review"].length,
    release: groupedTasks["Task release"].length,
    released: groupedTasks["Task released"].length,
  };

  updateTaskCounts(counts);
  updateProgressBar(counts);
  updateTaskTables(groupedTasks);
  addEventListeners();
}

function updateTaskCounts(counts) {
  Object.entries(counts).forEach(([key, value]) => {
    const element = document.getElementById(`${key}Count`);
    if (element) {
      element.innerText = value;
    } else {
      console.warn(`Element with id "${key}Count" not found`);
    }
  });
}

function updateProgressBar(counts) {
  document.getElementById("taskProgressBar").innerHTML = addProgressBar(
    counts.total,
    counts.notDone,
    counts.doing,
    counts.review,
    counts.release,
    counts.released
  );
}

function updateTaskTables(groupedTasks) {
  const taskStatuses = {
    notDoneTasks: STATUS.OPEN,
    doingTasks: STATUS.IN_PROGRESS,
    reviewTasks: STATUS.WAITING_REVIEW,
    releaseTasks: STATUS.WAITING_RELEASE,
    releasedTasks: null,
  };

  Object.entries(taskStatuses).forEach(([elementId, status]) => {
    const taskGroup = elementId.replace("Tasks", "");
    document.getElementById(elementId).innerHTML = createTaskTable(groupedTasks[`Task ${taskGroup}`], status);
  });
}

function addEventListeners() {
  addModalEventListeners();
  [STATUS.OPEN, STATUS.IN_PROGRESS, STATUS.WAITING_REVIEW, STATUS.WAITING_RELEASE].forEach(addBtnTaskStartListeners);
}

function addProgressBar(total, notStarted, inProgress, waitForReview, waitForRelease, released) {
  let progressBarHTML = `
      <div class='progress-bar progress-bar-striped bg-danger'
           role='progressbar'
           style='width: ${(notStarted / total) * 100}%'
           aria-valuenow='${(notStarted / total) * 100}'
           aria-valuemin='0'
           aria-valuemax='100'>
          ${((notStarted / total) * 100).toFixed(2)}%
      </div>
`;

  progressBarHTML += `
      <div class='progress-bar progress-bar-striped bg-warning'
           role='progressbar'
           style='width: ${(inProgress / total) * 100}%'
           aria-valuenow='${(inProgress / total) * 100}'
           aria-valuemin='0'
           aria-valuemax='100'>
          ${((inProgress / total) * 100).toFixed(2)}%
      </div>
`;

  progressBarHTML += `
<div class='progress-bar progress-bar-striped bg-warning'
     role='progressbar'
     style='width: ${(waitForReview / total) * 100}%'
     aria-valuenow='${(waitForReview / total) * 100}'
     aria-valuemin='0'
     aria-valuemax='100'>
    ${((waitForReview / total) * 100).toFixed(2)}%
</div>
`;

  progressBarHTML += `
<div class='progress-bar progress-bar-striped bg-warning'
     role='progressbar'
     style='width: ${(waitForRelease / total) * 100}%'
     aria-valuenow='${(waitForRelease / total) * 100}'
     aria-valuemin='0'
     aria-valuemax='100'>
    ${((waitForRelease / total) * 100).toFixed(2)}%
</div>
`;

  progressBarHTML += `
<div class='progress-bar progress-bar-striped bg-success'
     role='progressbar'
     style='width: ${(released / total) * 100}%'
     aria-valuenow='${(released / total) * 100}'
     aria-valuemin='0'
     aria-valuemax='100'>
    ${((released / total) * 100).toFixed(2)}%
</div>
`;

  return progressBarHTML;
}

// Hàm để tạo bảng từ dữ liệu tác vụ
function createTaskTable(tasks, status) {
  if (tasks.length === 0) {
    return "<p>No tasks available.</p>";
  }

  const headers = getTableHeaders(status);
  const rows = tasks.map((task) => createTableRow(task, status));

  return `
    <table class='table table-bordered'>
      <thead>
        <tr>${headers.join("")}</tr>
      </thead>
      <tbody>
        ${rows.join("")}
      </tbody>
    </table>
  `;
}

function getTableHeaders(status) {
  const baseHeaders = ["TaskID", "TaskName", "P.StartDate", "P.EndDate", "P.Effort", "PIC", "Reviewer"];

  if (status !== STATUS.OPEN) {
    // baseHeaders.push("A.StartDate", "A.EndDate", "A.Effort", "%A.Completed");
    baseHeaders.push("A.Effort", "%A.Completed");
  }

  if ([STATUS.OPEN, STATUS.IN_PROGRESS, STATUS.WAITING_REVIEW, STATUS.WAITING_RELEASE].includes(status)) {
    baseHeaders.push("Action");
  }

  baseHeaders.push("Link Refer");
  return baseHeaders.map((header) => `<th>${header}</th>`);
}

function createTableRow(task, status) {
  const cells = [
    `<td><a href="#" class="openModal link-info" data-taskid="${task[0]}">${task[0]}</a></td>`,
    `<td><span class="d-inline-block text-truncate" style="max-width: 50px;">${task[1]}</span></td>`,
    `<td>${excelDateToJSDate(task[5])}</td>`,
    `<td>${excelDateToJSDate(task[6])}</td>`,
    `<td>${task[8]}</td>`,
    `<td>${task[3]}</td>`,
    `<td>${task[4]}</td>`,
  ];

  if (status !== STATUS.OPEN) {
    cells.push(
      // `<td>${excelDateToJSDate(task[9])}</td>`,
      // `<td>${excelDateToJSDate(task[10])}</td>`,
      `<td>${task[11]}</td>`,
      `<td>${task[12] * 100}%</td>`
    );
  }

  cells.push(getActionButton(task, status));

  const link = {
    linkBD: task[17],
    linkDD: task[18],
    linkET: task[19],
    linkEV: task[20],
  };
  cells.push(createDropdown(link));

  return `<tr>${cells.join("")}</tr>`;
}
function createDropdown(link) {
  let dropdownItems = ""; // Chuỗi lưu trữ các mục trong dropdown

  // Kiểm tra và thêm thẻ <li> nếu link tồn tại
  if (link.linkBD) {
    dropdownItems += `<li><a class="dropdown-item" href="${link.linkBD}" target="_blank" rel="noopener noreferrer">Link BD</a></li>`;
  }
  if (link.linkDD) {
    dropdownItems += `<li><a class="dropdown-item" href="${link.linkDD}" target="_blank" rel="noopener noreferrer">Link DD</a></li>`;
  }
  if (link.linkET) {
    dropdownItems += `<li><a class="dropdown-item" href="${link.linkET}" target="_blank" rel="noopener noreferrer">Link Estimation</a></li>`;
  }
  if (link.linkEV) {
    dropdownItems += `<li><a class="dropdown-item" href="${link.linkEV}" target="_blank" rel="noopener noreferrer">Link Evidence</a></li>`;
  }

  // Nếu không có link nào tồn tại, thêm một mục "No Links"
  if (!dropdownItems) {
    dropdownItems = `<li><a class="dropdown-item" href="#">No Links Available</a></li>`;
  }

  // Trả về HTML hoàn chỉnh của dropdown
  return `
    <td>
      <div class="dropdown">
        <button class="btn btn-sm btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
          Link
        </button>
        <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
          ${dropdownItems}
        </ul>
      </div>
    </td>
  `;
}

function getActionButton(task, status) {
  const buttons = {
    [STATUS.OPEN]: {
      class: "btn-task-start btn-primary",
      icon: "bi bi-play",
      tooltip: "Click To Start Task",
      validateLink: task[18],
    },
    [STATUS.IN_PROGRESS]: {
      class: "btn-task-inprogress  btn-success",
      icon: "bi bi-check-circle",
      tooltip: "Mark as Completed",
      validateLink: task[17],
    },
    [STATUS.WAITING_REVIEW]: {
      class: "btn-task-review  btn-success",
      icon: "bi bi-shield-fill-check",
      tooltip: "Mark as Pass Review",
    },
    [STATUS.WAITING_RELEASE]: {
      class: "btn-task-release btn-success",
      icon: "bi bi-rocket",
      tooltip: "Click To Release Task",
    },
  };

  const button = buttons[status];
  if (!button) return "";

  return `
    <td>
      <button 
        class="btn btn-sm ${button.class}" 
        data-taskid="${task[0]}" 
        data-validateLink="${button.validateLink}"
        title="${button.tooltip}"
        data-toggle="tooltip" 
        data-placement="top"
      >
        <i class="${button.icon}"></i>
      </button>
      <button 
        class="btn btn-sm btn-danger btn-logwork"
        data-bs-toggle="modal"
        data-bs-target="#logWorkModal"
        data-taskid="${task[0]}"
        data-taskpic="${task[4]}"
        data-toggle="tooltip" 
        title="Click to logwork"
      >
        <i class="bi bi-clock-history"></i>
      </button>
    </td>
  `;
}

function addBtnTaskStartListeners(status) {
  const listenerConfig = {
    [STATUS.OPEN]: {
      elementId: "notDoneTasks",
      buttonClass: "btn-task-start",
      completionPercentage: 0.1,
      updateField: { index: 9, value: "A.StartDate" },
      validateLink: { flg: true, message: "Add link estimation vào, giỡn mặt hả???" },
    },
    [STATUS.IN_PROGRESS]: {
      elementId: "doingTasks",
      buttonClass: "btn-task-inprogress",
      completionPercentage: 0.8,
      validateLink: { flg: true, message: "Add link evidence vào, giỡn mặt hả???" },
    },
    [STATUS.WAITING_REVIEW]: {
      elementId: "reviewTasks",
      buttonClass: "btn-task-review",
      completionPercentage: 0.9,
      updateField: { index: 10, value: "A.EndDate" },
    },
    [STATUS.WAITING_RELEASE]: {
      elementId: "releaseTasks",
      buttonClass: "btn-task-release",
      completionPercentage: 1,
      updateField: { index: 13, value: "A.ReleaseDate" },
    },
  };

  const config = listenerConfig[status];
  if (!config) return;

  document.getElementById(config.elementId).addEventListener("click", handleTaskUpdate);

  function handleTaskUpdate(e) {
    if (e.target && e.target.classList.contains(config.buttonClass)) {
      if (config.validateLink) {
        const validLink = e.target.getAttribute("data-validateLink");
        if (!validateLink(validLink, config.validateLink.message)) return;
      }
      const taskId = e.target.getAttribute("data-taskid");
      updateTaskInExcel(taskId, config)
        .then(() => {
          loadFullname();
          showNotification("Task updated successfully!", "success");
        })
        .catch((error) => {
          console.log(error);
          showNotification(`Error: ${error.message}`, "danger");
        });
    }
  }
  function validateLink(input, message) {
    if (!input) {
      showNotification(`${message}`, "danger");
      return false;
    }
    return true;
  }

  function updateTaskInExcel(taskId, config) {
    return Excel.run((context) => {
      const wbsTable = context.workbook.tables.getItem("TblWbs");
      const wbsRange = wbsTable.getDataBodyRange();
      wbsRange.load("values");

      return context.sync().then(() => {
        const wbsData = wbsRange.values;
        const taskIndex = wbsData.findIndex((row) => row[0] === taskId);

        if (taskIndex !== -1) {
          const updatedRow = [...wbsData[taskIndex]];
          updatedRow[12] = config.completionPercentage; // %A.Completed

          if (config.updateField) {
            const today = new Date().toISOString().split("T")[0]; // Format: YYYY-MM-DD
            updatedRow[config.updateField.index] = jsDateToExcelDate(today);
          }

          wbsRange.getRow(taskIndex).values = [updatedRow];
        }

        return context.sync();
      });
    });
  }
}

function addModalEventListeners() {
  const openModalLinks = document.querySelectorAll(".openModal");
  const updateProgressModal = document.getElementById("updateProgressModal");
  const progressTaskIDInput = document.getElementById("progressTaskID");

  openModalLinks.forEach((link) => {
    link.addEventListener("click", handleModalOpen);
  });

  function handleModalOpen(event) {
    event.preventDefault();
    const taskId = this.getAttribute("data-taskid");
    progressTaskIDInput.value = taskId;
    getTaskByID(taskId);
    showModal();
  }

  function showModal() {
    // eslint-disable-next-line no-undef
    const myModal = new bootstrap.Modal(updateProgressModal);
    myModal.show();
  }
}
